import React, {useState} from 'react';
import { Paper,
         AppBar, 
         Toolbar,
         IconButton,
         Button,
         Typography, 
          
        } from '@material-ui/core';
import { makeStyles, MuiThemeProvider } from '@material-ui/core/styles';
import  SortIcon  from '@material-ui/icons/Sort';
import {Link} from 'react-router-dom'; 

const appbarStyles = makeStyles((theme)=>({
    root:{
        flexGrow:1,
        
        
    },
    container:{
        width: '100%',
        height: '10vh',
        backgroundColor: theme.palette.primary.main,
        fontFamily:'Roboto',
    },
    icons:{
        fontSize:'2rem',
        color:'#ffffff',
    },
    logo:{
        flexGrow:'1',
    },
    wrapper:{
        width:'80%',
        margin: '0 auto',
    }
    
}));

const Appbar=()=>{
    const appbar= appbarStyles();

    {/*const [selectedTabs,setSelectedTabs] = React.useState(0);

    const handleChange = (event, newValue) => {
        setSelectedTabs(newValue);
    };*/}


 

return(
    <div className={appbar.root}>
       
            <AppBar position="sticky" className={appbar.container} elevation={0}>
                <Toolbar className={appbar.wrapper}>
                    <h1 className={appbar.logo}>JourneyPartner.</h1>

                <Button variant="outlined" color="secondary">
                    Company
                    <Link to="/company"></Link>
                </Button>    
                <IconButton size="medium" aria-label="app" color="inherit">
                    <SortIcon className={appbar.icons}/>
                 </IconButton >
                 
                
                 </Toolbar>
            </AppBar>
        
    </div>
   


);

}
export default Appbar;