import React from 'react'
import { Paper, Grid, Typography, List , ListItem, ListItemText } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import {Link} from 'react-router-dom';

const footerStyles=makeStyles((theme)=>({
    root:{
        display: 'flex',
        flexWrap: 'wrap',
    },  
    grid:{
        backgroundColor: theme.palette.primary.main,
    },
    paper:{
        padding: theme.spacing(2),
        color: '#e5e5e5',
        textAlign: 'left',
        backgroundColor: theme.palette.primary.dark,
        height: '25vh',
        [theme.breakpoints.down('sm')]: {
            height: '40vh',
            textAlign: 'center',

          },
    },
    list:{
        [theme.breakpoints.down('sm')]: {
            textAlign:'center',

          },

    },
    
    footerCopyright:{
        width: '100%',
        textAlign: 'center',
        padding: theme.spacing(2),
        backgroundColor: theme.palette.primary.main,
        height: '8vh',
        fontWeight: 'bold',

        
    },
    
})); 

const Footer =()=>{

    const footer=footerStyles();

    return(
        <div className={footer.root}>
            <Grid container spacing={24} className={footer.grid}>
                <Grid item xs={12} sm={4}>
                <Paper className={footer.paper} square>
                        <Typography variant="h6">Journey Partner</Typography>
                        <Typography  variant="h7">We provide rides like nowhere in the world. Join the Journey like its your own ride and you will feel like your brother is leading you towards you Journey.
                        The trust like none other to be found. Feel Comfortable. Be our part of the Journey.
                        </Typography>
                </Paper>
                </Grid>
                <Grid item xs={12} sm={4}>
                <Paper className={footer.paper} square>
                        <Typography variant="h6">About Us</Typography>
                        <List className={footer.list}>
                            <ListItem>
                            <Link to="/company">Company</Link>    
                            </ListItem>
                            <ListItem>
                            <Link to="/termsandservices">Terms and Services</Link>    
                            </ListItem>
                            <ListItem>
                            <Link to="/privacypolicy">Privacy Policy</Link>    
                            </ListItem>
                        </List>
                    </Paper>
                </Grid>
                <Grid item xs={12} sm={4}>
                <Paper className={footer.paper} square>
                        <Typography variant="h6">Social</Typography>
                        <List className={footer.list}>
                            <ListItem >
                                <Link to="">Facebook</Link>      
                            </ListItem>
                            <ListItem>
                            <Link to="">Instagram</Link>    
                            </ListItem>
                            <ListItem>
                            <Link to="">Twitter</Link>    
                            </ListItem>
                        </List>
                    </Paper>
                </Grid>
                <Grid  item xs={12} sm={12}>
                <Paper className={footer.footerCopyright} square>
                        
                        &copy; {new Date().getFullYear()} Copyright: 
                        <Link to=""> Journey Partner</Link>
                    
                </Paper>
                </Grid>
            </Grid>
        </div>
       
        
    );

}
export default Footer;
