import React from 'react'
import {Grid,Paper,Typography} from '@material-ui/core'; 
import { makeStyles } from '@material-ui/core/styles';

const au2Styles=makeStyles((theme)=>({
    root:{
        display:'flex',
        flexWrap:'wrap',
    },
    container:{
        height:'50vh',
        width:'100%',
        backgroundColor: '#ffffff',
    },
    content:{
        padding: theme.spacing(4),
        color: '#000000',
        textAlign: 'left',
        backgroundColor: '#ffffff',
        width: '100%',
        height: '50vh',
    },
    pic:{
        color: '#000000',
        textAlign: 'left',
        backgroundColor:  '#ffffff',
        width: '100%',
        height: '50vh',
    },
    image:{
        height:'50vh',
        width:'100%',
    },
    highlight:{
        color: theme.palette.primary.main,
    }


}))

const AU2=()=> {

    const classes=au2Styles();

    return (
        <div className={classes.root}>
            <Grid container spacing={1} className={classes.root}>
                <Grid item xs={8} >
                    <Paper className={classes.content} square>
                        <Typography variant="h5" className={classes.highlight}>
                            <strong>Tired Of Travelling Alone? We Have A Solution To This Problem.</strong>                
                        </Typography>
                        <Typography variant="h7" >
                        If you are stopping yourself from travelling because you are fed up of travelling alone and because of high fares of Buses or Train, we have got you covered. Say No more...You are just one button away to book a ride with a person who is heading in same direction.
                        </Typography>
                        <Typography variant="h6" >
                            <br/>
                        Just download our mobile app and you can see the travel plans of the people travelling where you want to travel or send package.
                        </Typography>
                        <Typography variant="h7">
                        We connect people with drivers heading towards the same Destination. 
                        </Typography>
                        <Typography variant="h6" className={classes.highlight}>
                            <br/>
                        Passenger is Partner. Driver is Leader.  
                        </Typography>
                    </Paper>

                </Grid>
                <Grid item xs={4} >
                    <Paper className={classes.pic}  square>
                            <img className={classes.image}  src="/images/img-3.jpg" />
                    </Paper>

                </Grid>
            </Grid>
        </div>
    )
}
export default AU2;