import React from 'react';
import { Grid,Box,Button,Paper,Typography} from '@material-ui/core';
import Icon from '@material-ui/core/Icon';
import { makeStyles } from '@material-ui/styles';
import { Formik, Form , Field, ErrorMessage} from 'formik';
import { TextField } from 'formik-material-ui';
import * as Yup from 'yup';


const contactUsStyle=makeStyles((theme)=>({
    root:{
        display: 'flex',
        flexWrap: 'wrap',
        
       
    },
    grid:{
        height: '72vh',
        width: '100%',
        backgroundColor: theme.palette.secondary.light,
        [theme.breakpoints.down('sm')]: {
            height: '100vh',
            backgroundColor: "#ffffff",


          },

    },
    paper:{
        padding: theme.spacing(2),
        color: '#000000',
        textAlign: 'center',
        backgroundColor: "#fffffff",
        height: '20vh',
    },

    form:{
        padding: theme.spacing(2),
        color: '#000000',
        textAlign: 'center',
        backgroundColor: "#fffffff",
        height: '80vh',
        

    },
    box:{
        width:'100%',
        justifyContent: 'center',
    },
    field:{
        width:'100%',
    }
}))

const initailValues = {
    fullName: 'John Doe',
    email: 'jd@email.com',
    message: 'lorem ipsum lorem ipsum',
}

const onSubmit = values => {
    console.log('Form data', values);
}

const validationSchema = Yup.object({
    fullName: Yup.string().required("Full name is required"),
    email: Yup.string().email("Invalid email format").required("your email address is required"),
    message: Yup.string().max(150).required("Please enter your query"),
})

const ContactUs=()=>{

    const contactus=contactUsStyle();

    return(
        <div className={contactus.root}>
            <Grid conatiner spacing={24} className={contactus.grid}>
                <Grid item xs={12} sm={12} md={12} lg={12} xl={12} style={{height:'10vh'}}>
                <Paper className={contactus.paper} variant="outlined" elevation={3}>
                 <Typography color="primary" variant="h3">
                      Conatct Us
                 </Typography>
                 </Paper>
                </Grid>
                <Grid item xs={12} sm={12}>
                <Paper className={contactus.form} variant="outlined" elevation={3} >
                 <Formik
                    initialValues={initailValues}
                    validationSchema={validationSchema}
                    onSubmit={onSubmit}
                 >
                     
                     <Form> 
                         <Box className={contactus.box} margin={1}>
                            <Field 
                                className={contactus.field}
                                label="Full Name" 
                                component={TextField} 
                                id="fullName"
                                name="fullName" 
                                type="text"
                                variant="outlined"
                                helperText="Please enter your full name"
                            />
                            <ErrorMessage name="fullName" />
                         </Box>
                         <Box className={contactus.box} margin={1}>
                            <Field 
                                className={contactus.box}
                                label="e-mail" 
                                component={TextField} 
                                id="email"
                                name="email" 
                                type="email"
                                variant="outlined" 
                                helperText="Please enter your email address" 
                            />
                            <ErrorMessage name="email" />
                         </Box>
                         <Box className={contactus.box} margin={1}>
                            <Field 
                                className={contactus.box}
                                label="Message" 
                                component={TextField} 
                                id="message"
                                name="message" 
                                type="text"
                                multiline
                                rows={4}
                                variant="outlined"
                                helperText="Please enter your query"
                            />
                            <ErrorMessage name="message" />
                         </Box>
                         <Box className={contactus.box} margin={1}>
                            <Button 
                                className={contactus.box}
                                variant="contained"
                                color="primary"
                                size="large"
                               // disabled={isSubmitting}
                                onClick={onSubmit}
                                endIcon={<Icon>send</Icon>}
                            >
                                 Submit
                            </Button>
                         </Box>
                         
                     </Form>
                     
                 </Formik>
                 </Paper>
                </Grid>
               
            </Grid>
        
        </div>
        
    );
}
export default ContactUs;