import React from 'react'
import { Card, 
        CardContent,
        Grid, 
        Typography, 
        Box, 
        Button,
        FormControlLabel,
        Radio,
        InputLabel, 
        } from '@material-ui/core'; 
import Icon from '@material-ui/core/Icon'
import { makeStyles } from '@material-ui/styles';
import {Formik, Form, Field, } from 'formik';
import { TextField, RadioGroup, } from 'formik-material-ui';
import * as Yup from 'yup';
import Link from 'react-router-dom';

const BasicInfoFormStyles=makeStyles((theme)=>({
    container:{
        display: 'flex',
        flexWrap: 'wrap',
        backgroundColor: theme.palette.primary.light,
        height: '140vh',
        },
    heading:{
        height:'20vh',
        color: '#000000',
        textAlign: 'center',
        backgroundColor: theme.palette.primary.light,
        padding: theme.spacing(1),
        },
    form:{
            padding: theme.spacing(2),
            color: '#000000',
            textAlign: 'center',
            backgroundColor: theme.palette.primary.light,
            height: '120vh',
        },
    field:{
        justifyContent:'center',
        width: '100%',
        paddingBottom: theme.spacing(1),

    },
    btn:{
        backgroundColor: theme.palette.primary.light,

    }
}))

const initialValues={
    
    firstName:'',
    lastName:'',
    gender:'',
    contactNo:'',
    email:'',
    password: '',
    confirm_password: '',
    
}

const validationSchema=Yup.object({
    firstName: Yup.string().required("First Name is required!"),
    lastName: Yup.string().required("Last Name is required!"),
    gender: Yup.object().shape({
        gender: Yup.string().required("Gender is required")}),
    contactNo: Yup.string().length(13).required("Use exact format!"),
    email: Yup.string().email("Invalid email format").required("Your E-mail address is required"),
    password: Yup.string().min(8).required("Alpha numeric Password is Required!"),
    confirm_password:Yup.string().required("Retype Correct Password").oneOf([Yup.ref('password'), null], 'Passwords must match',),
})

const onSubmit=()=>{}


 const BasicInfoForm=()=> {

    const form1=BasicInfoFormStyles();

    return (
        <Grid container spacing={24} className={form1.container}>
            
            
            <Grid item xs={12} className={form1.form}>
            <Card>
                <CardContent>
                <Typography variant="h4" color="primary" >SignUp As Driver</Typography>
                <Formik 
                 initialValues={initialValues}
                 validationSchema={validationSchema}
                 onSubmit={onSubmit}
                 
                >
                    <Form>
                        
                        <Box className={form1.field} margin={1}>
                            <Field 
                                name="firstName" 
                                type="text" 
                                id="firstName"
                                variant="outlined"
                                component={TextField} 
                                label="First Name"
                                helperText="Please Enter Your First Name"
                            />
                        </Box>
                        <Box className={form1.field} margin={1}>
                            <Field 
                                name="lastName" 
                                type="text" 
                                id="lastName"
                                variant="outlined"
                                component={TextField} 
                                label="Last Name"
                                helperText="Please Enter Your Last Name"
                            />
                        </Box> 
                        
                        <Box className={form1.field} margin={1}>
                            <InputLabel>Gender</InputLabel>
                            <Field 
                                name="gender" 
                                type="text" 
                                id="gender"
                                variant="outlined"
                                component={RadioGroup} 
                                label="Gender"
                                helperText="Choose carefully!"
                            >
                                <FormControlLabel className={form1.field}
                                name="gender"
                                  value="male"
                                  control={<Radio/>}
                                  label="Male"
                                />
                                <FormControlLabel className={form1.field}
                                  name="gender"
                                  value="female"
                                  control={<Radio  />}
                                  label="Female"
                                 />
                                 <FormControlLabel className={form1.field}
                                 name="gender"
                                   value="others"
                                   control={<Radio />}
                                   label="Others"
                                  />
                                
                            </Field>
                        </Box>
                        <Box className={form1.field} margin={1}>
                            <Field 
                                name="contactNo" 
                                type="text" 
                                id="contactNo"
                                variant="outlined"
                                component={TextField} 
                                label="Contact Number"
                                helperText="03** *** ****"
                            />
                        </Box>
                        <Box className={form1.field} margin={1}>
                            <Field 
                                name="email" 
                                type="email" 
                                id="email"
                                variant="outlined"
                                component={TextField} 
                                label="E-mail"
                                helperText="Please Enter Your E-mail address"
                            />
                        </Box>
                          
                        <Box className={form1.field} margin={1}>
                            <Field 
                                name="password" 
                                type="password" 
                                id="password"
                                variant="outlined"
                                component={TextField} 
                                label="Password"
                                helperText="Password must be consists of 8-alphanumeric digits"
                            />
                        </Box>
                        <Box className={form1.field} margin={1}>
                            <Field 
                                name="confirm_password" 
                                type="password" 
                                id="confirm_password"
                                variant="outlined"
                                component={TextField} 
                                label="Confrim Password"
                                helperText="Retype Your Password!"
                            />
                        </Box>  
                        <Box className={form1.Field} margin={1}>
                            <Button 
                               
                                variant="contained"
                                size="large"
                                color="primary"
                                onClick={onSubmit}
                                endIcon={<Icon>send</Icon>}
                            >
                                Vehicle Details
                                
                                
                                
                                    
                            </Button>
                        </Box>                 
                    </Form>
                </Formik>
                </CardContent>
            </Card>
                
            
            </Grid>
             

        </Grid>
       
    )
}
export default BasicInfoForm;




