import React from 'react'
import {Grid,Paper} from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';

const ts1Styles=makeStyles((theme)=>({
    root:{
        display:'flex',

    },
    container:{
        backgroundColor: "#ffffff",
        height: '90vh',
    },
 
    paper:{
        
        color: '#000000',
        textAlign: 'center',
        backgroundColor: theme.palette.primary.light,
        height: '90vh',
        width: '100%',

    },
    image:{
        height:'90vh',
    },

}));

const TS1=()=> {

    const classes=ts1Styles();


    return (
        <div className={classes.root}>
            <Grid spacing={0} 
                container 
                className={classes.container}>
                
                    <Paper className={classes.paper} square>
                        <img className={classes.image} style={{width: '100%'}} src="/images/img-5.jpg" />
                    </Paper>
                  
            </Grid>
        </div>
    )
}
export default TS1;
