import React from 'react';
import {    Grid,
            Paper,
            Typography,
            
} from '@material-ui/core';
import { makeStyles } from '@material-ui/styles';


const layout2Styles=makeStyles((theme)=>({
    root:{
        display:'flex',
        flexWrap: 'wrap',

    },
    container:{
        backgroundColor: theme.palette.secondary.light,
        height: '20vh',
    },
 
    heading:{
        padding: theme.spacing(2),
        color: '#000000',
        textAlign: 'center',
        backgroundColor: "#fffffff",
        height: '20vh',

    },
    services:{
        padding: theme.spacing(2),
        color: '#000000',
        textAlign: 'center',
        backgroundColor: "#fffffff",
        height: '50vh',
    }
   

}));

const Layout2=()=>{

    const classes=layout2Styles();

    return(
        <div className={classes.root}>
            <Grid spacing={2} 
                container 
                className={classes.container}>
                <Grid item xs={12}>
                    <Paper className={classes.heading} variant="outlined" elevation={3} square >
                        <Typography variant='h3'>Our Services</Typography>
                    </Paper>
                </Grid>           
            </Grid>
            <Grid container>
            <Grid item xs={12} sm={4} md={4} lg={4} xl={4} >
                    <Paper className={classes.services} variant="outlined"  elevation={0} square >
                        <Typography variant='h6'>1</Typography>
                    </Paper>
                </Grid><Grid item xs={12} sm={4} md={4} lg={4} xl={4}>
                    <Paper className={classes.services} variant="outlined"  elevation={0} square >
                        <Typography variant='h6'>2</Typography>
                    </Paper>
                </Grid><Grid item xs={12} sm={4} md={4} lg={4} xl={4}>
                    <Paper className={classes.services} variant="outlined" elevation={0} square >
                        <Typography variant='h6'>3</Typography>
                    </Paper>
                </Grid>
            </Grid>           
        </div>
    );
}
export default Layout2;