import React from 'react'
import {Grid,Paper,Typography,IconButton} from '@material-ui/core'; 
import { makeStyles } from '@material-ui/core/styles';
import CheckIcon  from '@material-ui/icons/Check';

const au4Styles=makeStyles((theme)=>({
    root:{
        display:'flex',
        flexWrap:'wrap',
    },
    container:{
        height:'40vh',
        width:'100%',
        backgroundColor: theme.palette.secondary.light,
    },
    content:{
        padding: theme.spacing(4),
        color: '#000000',
        textAlign: 'left',
        backgroundColor: "#ffffff",
        width: '100%',
        height: '40vh',
    },
    pic:{
        color: '#000000',
        textAlign: 'left',
        backgroundColor: "#ffffff",
        width: '100%',
        height: '40vh',
    },
    image:{
        height:'40vh',
        width:'100%',
    },
    icons:{
        fontSize:'1rem',
        color: theme.palette.primary.light,
    },
    highlight:{
        color: theme.palette.primary.light,
    }


}))

const AU4=()=> {

    const classes=au4Styles();

    return (
        <div className={classes.root}>
            <Grid container spacing={1} className={classes.root}>
            
                <Grid item xs={8} >
                    <Paper className={classes.content} square>
                        <Typography variant="h5" className={classes.highlight}>
                            <strong>How Journey Partner Works?(For Leader)</strong>                
                        </Typography>
                        <Typography variant="h7" >
                            <IconButton size="medium" color="inherit">
                                <CheckIcon className={classes.icons}/>
                            </IconButton > <strong className={classes.highlight}>Step 1: </strong>Leader Can Post Their Travel Plan By Entering The Specific Information Of Departure.                 
                         </Typography>
                         <Typography variant="h7" >
                             <br/>
                            <IconButton size="medium" color="inherit">
                                <CheckIcon className={classes.icons}/>
                            </IconButton > <strong className={classes.highlight}>Step 2: </strong>Share Travel Plans In Advance, Travel With Other Partners Heading In The Same Direction And Enjoy Shared Fare.
                       
                         </Typography>
                         <Typography variant="h7" >
                             <br/>
                            <IconButton size="medium" color="inherit">
                                <CheckIcon className={classes.icons}/>
                            </IconButton > <strong className={classes.highlight}>Step 3: </strong>Leader Can Confirm The Ride Whether He/She Wants To Share It With Their Partners.                      
                         </Typography>
                         
                        
                    </Paper>

                </Grid>

                <Grid item xs={4} >
                    <Paper className={classes.pic}  square>
                            <img className={classes.image}  src="/images/img-6.jpg" />
                    </Paper>

                </Grid>
                
            </Grid>
        </div>
    )
}
export default AU4;