import React from 'react'
import { Card, 
        CardContent,
        Grid, 
        Typography, 
        Box, 
        Button,
        } from '@material-ui/core'; 
import Icon from '@material-ui/core/Icon'
import { makeStyles } from '@material-ui/styles';
import {Formik, Form, Field, } from 'formik';
import { TextField } from 'formik-material-ui';
import * as Yup from 'yup';


const VehicleDetailFormStyles=makeStyles((theme)=>({
    container:{
        display: 'flex',
        flexWrap: 'wrap',
        backgroundColor: theme.palette.primary.light,
        height: '140vh',
        },
    heading:{
        height:'20vh',
        color: '#000000',
        textAlign: 'center',
        backgroundColor: theme.palette.primary.light,
        padding: theme.spacing(1),
        },
    form:{
            padding: theme.spacing(2),
            color: '#000000',
            textAlign: 'center',
            backgroundColor: theme.palette.primary.light,
            height: '120vh',
        },
    field:{
        justifyContent:'center',
        width: '100%',
        paddingBottom: theme.spacing(1),

    },
    btn:{
        backgroundColor: theme.palette.primary.light,

    }
}))

const initialValues={
    
    cnic:'',
    license:'',
    vehicleNo:'',
    vehicleCompany:'',
    engineCapacity:'',
    vehicleColor: '',
    
    
}

const validationSchema=Yup.object({
    cnic: Yup.number().max(13).required("CNIC is required!"),
    license: Yup.string().required("License Number is required!"),
    vehicleNo: Yup.string().required("Vehicle Number is required!"),
        
    vehicleName: Yup.string().required("Vehicle Name is required!"),
    engineCapacity: Yup.string().required("Engine Capacity is required"),
    vehicleColor: Yup.string().required("Vehicle Color is Required!"),
})

const onSubmit=()=>{}


 const VehicleDetailForm=()=> {

    const form2=VehicleDetailFormStyles();

    return (
        <Grid container spacing={24} className={form2.container}>
            
            
            <Grid item xs={12} className={form2.form}>
            <Card>
                <CardContent>
                <Typography variant="h4" color="primary" >SignUp As Driver</Typography>
                <Formik 
                 initialValues={initialValues}
                 validationSchema={validationSchema}
                 onSubmit={onSubmit}
                 
                >
                    <Form>
                        
                        <Box className={form2.field} margin={1}>
                            <Field 
                                name="cnic" 
                                type="text" 
                                id="cnic"
                                variant="outlined"
                                component={TextField} 
                                label="CNIC"
                                helperText="42401********"
                            />
                        </Box>
                        <Box className={form2.field} margin={1}>
                            <Field 
                                name="license" 
                                type="text" 
                                id="license"
                                variant="outlined"
                                component={TextField} 
                                label="License Number"
                                helperText="RN-**-***"
                            />
                        </Box> 
                        
                        <Box className={form2.field} margin={1}>
                            <Field 
                                name="vehicleNo" 
                                type="text" 
                                id="vehicleNo"
                                variant="outlined"
                                component={TextField} 
                                label="Vehicle Number"
                                helperText="LEA ****"
                            />
                        </Box>
                        <Box className={form2.field} margin={1}>
                            <Field 
                                name="vehicleName" 
                                type="text" 
                                id="vehicleName"
                                variant="outlined"
                                component={TextField} 
                                label="Vehicle Company"
                                helperText="Honda Civic"
                            />
                        </Box>
                        <Box className={form2.field} margin={1}>
                            <Field 
                                name="engineCapacity" 
                                type="text" 
                                id="engineCapacity"
                                variant="outlined"
                                component={TextField} 
                                label="Engine Capacity"
                                helperText="1300 cc"
                            />
                        </Box>
                          
                        <Box className={form2.field} margin={1}>
                            <Field 
                                name="vehicleColor" 
                                type="text" 
                                id="vehicleColor"
                                variant="outlined"
                                component={TextField} 
                                label="Vehicle Color"
                                helperText="White etc."
                            />
                        </Box>
                        
                        <Box className={form2.Field} margin={1}>
                            <Button 
                               
                                variant="contained"
                                size="large"
                                color="primary"
                                onClick={onSubmit}
                                endIcon={<Icon>send</Icon>}
                            >  
                                Finish
                                       
                            </Button>
                        </Box>                 
                    </Form>
                </Formik>
                </CardContent>
            </Card>
                
            
            </Grid>
             

        </Grid>
       
    )
}
export default VehicleDetailForm;




