import React from 'react'
import {Grid,Paper,Typography,IconButton} from '@material-ui/core'; 
import { makeStyles } from '@material-ui/core/styles';
import CheckIcon  from '@material-ui/icons/Check';

const au3Styles=makeStyles((theme)=>({
    root:{
        display:'flex',
        flexWrap:'wrap',
    },
    container:{
        height:'40vh',
        width:'100%',
        backgroundColor: theme.palette.secondary.light,
    },
    content:{
        padding: theme.spacing(4),
        color: '#000000',
        textAlign: 'left',
        backgroundColor: "#e5e5e5",
        width: '100%',
        height: '40vh',
    },
    pic:{
        color: '#000000',
        textAlign: 'left',
        backgroundColor: "#e5e5e5",
        width: '100%',
        height: '40vh',
    },
    image:{
        height:'40vh',
        width:'100%',
    },
    icons:{
        fontSize:'1rem',
        color: theme.palette.primary.dark,
    },
    highlight:{
        color: theme.palette.primary.dark,
    }


}))

const AU3=()=> {

    const classes=au3Styles();

    return (
        <div className={classes.root}>
            <Grid container spacing={1} className={classes.root}>
            <Grid item xs={4} >
                    <Paper className={classes.pic}  square>
                            <img className={classes.image}  src="/images/img-4.jpg" />
                    </Paper>

                </Grid>
                <Grid item xs={8} >
                    <Paper className={classes.content} square>
                        <Typography variant="h5" className={classes.highlight}>
                            <strong>How Journey Partner Works?(For Passenger)</strong>                
                        </Typography>
                        <Typography variant="h7" >
                            <IconButton size="medium" color="inherit">
                                <CheckIcon className={classes.icons}/>
                            </IconButton > <strong className={classes.highlight}>Step 1: </strong>Partner Enters Their Destination By Allowing The App To Access his/her Location.                       
                         </Typography>
                         <Typography variant="h7" >
                             <br/>
                            <IconButton size="medium" color="inherit">
                                <CheckIcon className={classes.icons}/>
                            </IconButton > <strong className={classes.highlight}>Step 2: </strong>Partner Can Select The Plan Best Suited And Then Increase or Decrease The Number Of Seats.                       
                         </Typography>
                         <Typography variant="h7" >
                             <br/>
                            <IconButton size="medium" color="inherit">
                                <CheckIcon className={classes.icons}/>
                            </IconButton > <strong className={classes.highlight}>Step 3: </strong>On The Scheduled Date, Partner Can Track The Leader's Location.                       
                         </Typography>
                         
                        
                    </Paper>

                </Grid>
                
            </Grid>
        </div>
    )
}
export default AU3;