import React from 'react'
import {Grid,Paper,Typography} from '@material-ui/core'; 
import { makeStyles } from '@material-ui/core/styles';

const ts2Styles=makeStyles((theme)=>({
    root:{
        display:'flex',
        flexWrap:'wrap',
    },
    container:{
        height:'100vh',
        width:'100%',
        backgroundColor: '#ffffff',
    },
    content:{
        padding: theme.spacing(4),
        color: '#000000',
        textAlign: 'left',
        backgroundColor: '#ffffff',
        width: '100%',
        height: '100vh',
    },
    
    highlight:{
        color: theme.palette.primary.main,
    }


}))

const TS2=()=>{
    const classes=ts2Styles();

    return (
        <div className={classes.root}>
            <Grid container spacing={1} className={classes.root}>
                <Grid item xs={12} >
                    <Paper className={classes.content} square>
                        <Typography variant="h5" className={classes.highlight}>
                            <strong>Terms of Services</strong>                
                        </Typography>
                        <Typography variant="h7" >
                        The Journey Patrtner Platform
                        
                        </Typography>
                        <br/>
                        <Typography variant="h7">
                    <br/>
                    The Journey Partner Platform provides a digital network which functions as a marketplace where persons (Users) who seek personal transportation services, the delivery of goods can be matched with persons , including drivers who can provide the Services. 
                    </Typography>
                    <br/>
                    <Typography variant="h7">
                    <br/>Each User shall create a User account that enables access to the Journey Partner Platform. 
                    </Typography>
                    <br/>
                    <Typography variant="h7">
                    <br/>Any decision by a User to make use of or accept Services is a decision made in such Users sole discretion. 
                    </Typography>
                    <br/>
                    <Typography variant="h7">
                    <br/>Each Service provided by a driver or other third party provider to a User shall constitute a separate agreement between such persons.
                    </Typography>
                    <br/>
                    <Typography variant="h7">
                    <br/>
                    Materials posted on the Journey Partner Platform are not intended to amount to advice on which reliance should be placed. 
                    </Typography>
                    <br/>
                    <Typography variant="h7">
                    <br/>We therefore disclaim all liability and responsibility arising from any reliance placed on such materials by any visitor to the Journey Partner Platform, or by anyone who may be informed of any of its contents.
                    </Typography>
                    <br/>
                    <Typography variant="h7">
                    <br/>
                        We aim to update the Journey Partner Platform regularly and may change the content at any time. 
                    </Typography>
                    <br/>
                    <Typography variant="h7">
                    <br/>If the need arises, we may suspend access to the Journey Partner Platform and the Services or close them indefinitely. 

                    </Typography>
                    <br/>
                    <Typography variant="h7">
                    <br/>Any of the material on the Journey Partner Platform or the Services may be out of date at any given time, and we are under no obligation to update such material.

                    </Typography>
                    <br/>
                    <Typography variant="h7">
                    <br/>
                        YOU ACKNOWLEDGE THAT NEITHER Journey Partner NOR ITS AFFILIATES PROVIDE TRANSPORTATION, GOODS OR LOGISTICS SERVICES OR FUNCTION AS A TRANSPORTATION, GOODS OR LOGISTICS CARRIER OR A TRANSPORTATION, GOODS OR LOGISTICS SERVICE PROVIDER, OR OWN ANY VEHICLES FOR TRANSPORTATION, GOODS OR LOGISTICS AND THAT ALL SUCH SERVICES ARE PROVIDED BY INDEPENDENT THIRD PARTY CONTRACTORS WHO ARE NOT EMPLOYED BY Journey Partner OR ANY OF ITS AFFILIATES.
                    </Typography>
                    </Paper>
                   

                </Grid>
                
            </Grid>
        </div>
    )
}
export default TS2;