import React from 'react';
import {    Grid,
            Paper,
        } from '@material-ui/core';
import { makeStyles } from '@material-ui/styles';

const layout1Styles=makeStyles((theme)=>({
    root:{
        display:'flex',

    },
    container:{
        backgroundColor: "#ffffff",
        height: '90vh',
    },
 
    paper:{
        
        color: '#000000',
        textAlign: 'center',
        backgroundColor: theme.palette.primary.light,
        height: '90vh',
        width: '100%',

    },

}));

const Layout1=()=>{

    const classes=layout1Styles();

    return(
        <div className={classes.root}>
            <Grid spacing={0} 
                container 
                className={classes.container}>
                
                    <Paper className={classes.paper} square>
                        <img style={{width: '100%'}} src="/images/img-header.jpg" />
                    </Paper>
                  
            </Grid>
        </div>
    );
}
export default Layout1;