import React from 'react';
import {    Grid,
            Paper,
            Typography,
            Button,
} from '@material-ui/core';
import { makeStyles } from '@material-ui/styles';
//for button link 
import { Link  } from 'react-router-dom';



const layout2Styles=makeStyles((theme)=>({
    root:{
        display:'flex',
        flexGrow: 1,

    },
    container:{
        backgroundColor: theme.palette.secondary.light,
        height: '70vh',
        width: '100%',
    },
 
    heading:{
        padding: theme.spacing(2),
        color: '#000000',
        textAlign: 'center',
        backgroundColor: "#e5e5e5",
        width: '100%',
        height: '20vh',
    },
    content:{
        padding: theme.spacing(2),
        color: '#000000',
        textAlign: 'center',
        backgroundColor: "#e5e5e5",
        width: '100%',
        height: '40vh',
    },
    btn:{
        width: '100%',
        color: '#000000',
        textAlign: 'center',
        backgroundColor: "#e5e5e5",
        height:'10vh',
    },
    btnLink:{
        padding: theme.spacing(2),
    
        
    }
   

}));

const Layout2=()=>{

    const classes=layout2Styles();

    return(
        <div className={classes.root}>
            <Grid spacing={24} 
                container 
                direction="row"
                justify="center"
                alignItems="center"
                className={classes.container}>
                <Grid item md={12} sm={12} xs={12} lg={12} xl={12}>
                <Paper className={classes.heading} variant="outlined" elevation={3} square >
                    <Typography variant='h2'>how to be journey leader</Typography>
                    
                </Paper>
                </Grid>  
                <Grid item md={12} sm={12} xs={12} lg={12} xl={12}>
                        <Paper className={classes.content} variant="contained" elevation={3} square >
                        <Typography variant='h7'>
                        Journey Leader is the one who leads the way to the adventurous ride along with his/her curious Journey Partners.
                    </Typography> 
                        </Paper>
                    </Grid>
                <Grid item md={12} sm={12} xs={12} lg={12} xl={12}>
                        <Paper className={classes.btn} variant="contained" elevation={3} square >
                        <Button className={classes.btnLink} variant="contained" color="primary">
                             <Link  to="/signup">
                                 SignUp As Journey Leader
                             </Link>    
                         </Button>
                        </Paper>
                    </Grid>
            </Grid>
        </div>
    );
}
export default Layout2;